<template>
  <div class="flex xl:flex-row flex-col justify-between mb-8">
    <!-- <div class="lg:flex lg:divide-x lg:divide-grey-01 xl:mb-0 mb-4">
      <div class="flex lg:pr-4 lg:mb-0 mb-4">
        <router-link :to="{ name: 'setting' }" class="text-dark mr-4"><span class="material-icons">chevron_left</span></router-link>
        <h5>Add Inventory Condition</h5>
      </div>
      <div class="lg:pl-4">
        <Breadcrumb :items="breadcrumb">
          <span class="material-icons">chevron_right</span>
        </Breadcrumb>
      </div>
    </div> -->
    <Breadcrumb pageTitle="Add Inventory Condition" :backTo="{ name: 'setting' }" :items="breadcrumb" />
  </div>
  <Card>
    <div class="flex flex-col mt-4">
      <InventoryConditionForm ref="refConditionForm" @change="handleConditionChange" />
    </div>
    <template #footer>
      <div class="flex justify-end">
        <Button color="teal" view="smooth" @click="handleCreateInventoryCondition" :loading="loading.create">Add</Button>
      </div>
    </template>
  </Card>
</template>

<script>
import { defineComponent, ref } from 'vue'
import { useRouter } from 'vue-router'
import { useInventoryCondition } from 'composable/setting'
import Card from 'bbUi/components/card/card.vue'
import Input from 'bbUi/components/input/input.vue'
import Button from 'bbUi/components/button/button.vue'
import Breadcrumb from 'components/Breadcrumb.vue'
import InventoryConditionForm from 'components/InventoryCondition/Form.vue'
import { ElMessage } from 'element-plus'

export default defineComponent({
  components: {
    Card,
    Input,
    Button,
    Breadcrumb,
    InventoryConditionForm
  },
  setup(props, { emit }) {
    const refConditionForm = ref(null)
    const { loading, formData, actionCreate } = useInventoryCondition()
    const router = useRouter()
    const breadcrumb = [{
      text: 'Settings',
      to: {
        name: 'setting'
      }
    }, {
      text: 'Inventory Condition',
      to: {
        name: 'settingInventoryCondition'
      },
      
    }, {
      text: 'Add Condition'
    }]
    const handleConditionChange = (data) => {
      Object.assign(formData, {
        condition_name: data.condition_name,
        // condition_term: data.terms
      })
    }
    const handleCreateInventoryCondition = async () => {
      const isValid = refConditionForm.value.validate ? refConditionForm.value.validate() : false
      if(isValid){
        await actionCreate(() => {
          router.push({
            name: 'settingInventoryCondition'
          })
          ElMessage({
            type: 'success',
            message: 'New condition created'
          })
        })
      }
    }
    
    return {
      refConditionForm,
      breadcrumb,
      loading,
      handleConditionChange,
      handleCreateInventoryCondition
    }
  }
})
</script>